p {
  line-height: 130%;
}

h1 {
  text-align: center;
  margin: 0;
  padding: 15px;
  font-size: 16pt;
}

.pfx-backbutton {
  margin-top: 20px;
}

.ladderlist {
  max-width: 240px;
}

.ladder {
  max-width: 600px;
  padding: 0 15px 15px;
}

.ladder table {
  margin: 0 auto;
}

.ladder table, .ladder td, .ladder th {
  border-collapse: collapse;
  border: 1px solid #bbb;
}

.ladder td, th {
  padding: 3px 5px;
}

.ladder th {
  text-align: left;
  background: #eee;
  font-size: 9pt;
